import notification from "@/notifications";

function formatDate(inputDate: string, inputTime: string): string {        
    const [day, month, year] = inputDate.split('.');
    
    if (!day || !month || !year || day.length !== 2 || month.length !== 2 || year.length !== 4) {
        const errorMsg = 'Некорректный формат даты. Ожидается: дд.мм.ГГГГ (например, "12.05.2023")';
        notification.error(errorMsg);
        throw new Error(errorMsg);
    }
        
    const [hours, minutes] = inputTime.split(':');
    
    if (!hours || !minutes || hours.length !== 2 || minutes.length !== 2) {
        const errorMsg = 'Некорректный формат времени. Ожидается: ЧЧ:мм (например, "15:20")';
        notification.error(errorMsg);
        throw new Error(errorMsg);
    }
    
    const timePart = `${hours}:${minutes}:00`;
        
    const formattedDate = `${year}-${month}-${day} ${timePart}`;
        
    const dateObj = new Date(formattedDate);
    if (isNaN(dateObj.getTime())) {        
        const errorMsg = `Некорректная дата: "${inputDate}" (например, "31.02.2023")`;
        notification.error(errorMsg);
        throw new Error(errorMsg);
    }

    return formattedDate;
}

export default formatDate;