<template>
  <textarea
    v-model="valueTextArea"
    :placeholder="placeholder"
    v-focus-class
    class="textarea"
    :disabled="props.disabled"
  ></textarea>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits, onMounted } from "vue";
import { vFocusClass } from "@/directives/FocusDirective";

const emit = defineEmits(["update:modelValue", "update:is-valid"]);

const props = defineProps({
  modelValue: {
    type: String,
    required: true,
    default: "",
  },
  isValid: {
    type: Boolean,
    required: false,
    default: true,
  },
  disabled: {
    type: Boolean,
    required: false,
    default: false,
  },
  placeholder: {
    type: String,
    default: "",
  },
});

const valueTextArea = ref(props.modelValue);

watch(
  () => props.modelValue,
  (newVal) => {
    valueTextArea.value = newVal;
  }
);

watch(valueTextArea, (newVal) => {
  emit("update:modelValue", newVal);
  emit("update:is-valid", newVal.trim().length > 0);
});

onMounted(() => {
  emit("update:is-valid", props.modelValue.trim().length > 0);
});
</script>

<style lang="scss">
textarea {
  height: 150px;
}
</style>
